











































































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
import editImage from '../../components/editImage.vue'
export default {
	data() {
		return {
			isFix: false,
			src: '',
			showVideo: false,
			content: "",
			comlist: [],
			imageList: [],
			imgurlarr: [],
		}
	},
	computed: {
		...mapState(['user']),
	},
	components: {
		editImage
	},
	onUnload() {
		this.src = ''
	},
	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.redirectTo({
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		getimglist: function(data) {
			let that = this;
			this.imageList = data;
			this.imageList.forEach(function(value, key) {
				//console.log(value);
				that.uploadImage(value);
			})
		},
		getcomlist: function() {

			let that = this;
			//console.log(this.user.user.token);

			uni.request({
				url: common.hosturl + "index/getmessage.html",
				method: "POST",
				data: {
					token: this.user.user.token,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {

					//console.log(JSON.stringify(res.data))
					if (res.data.code == 0) {

						that.comlist = res.data.list;

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})
		},

		uploadImage: function(e) {

			let that = this;
			console.log(e);
			const uploadTask = uni.uploadFile({
				url: common.hosturl + "Upload/uploadfile.html",
				filePath: e,
				name: "file",
				formData: {
					token: this.user.user.token
				},
				success: (uploadFileRes) => {
					let res = JSON.parse(uploadFileRes.data);
					if (res.code == 0) {
						this.imgurlarr = this.imgurlarr.concat(res.fileurl);

						//console.log(JSON.stringify(this.imgurlarr));
					} else {


					}


				},
			});

			uploadTask.onProgressUpdate((res) => {
				/* console.log('上传进度' + res.progress);
				console.log('已经上传的数据长度' + res.totalBytesSent);
				console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend); */

			});
		},

		postcontent: function() {
			if (this.imgurlarr == "") {
				uni.showToast({
					icon: 'none',
					title: '请上传图片'
				});
				return;
			}

			//let lifeimage=this.imgurlarr.join("|");
			let data = {
				'lifeimage': this.imgurlarr,
				'token': this.user.user.token,
				'lifecontent': this.content,
				'lifecat': 1,
			};
			var that = this;
			uni.request({
				url: common.hosturl + "life/savelife.html",
				method: "post",
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: data,
				success: function(res) {
					console.log(res.data);
					if (res.data.code == 0) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
							success: function() {

								uni.redirectTo({
									url: "tranInfo"
								})
							}

						});

					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});

					}

				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}

			})
		},
		delImage: function(e) {
			console.log(e);
			this.imageList.splice(e, 1);
			this.imgurlarr.splice(e, 1);
			console.log(this.imgurlarr);
		},
		showmessage: function(e) {
			this.content = this.comlist[e].content;
			this.isFix = false;

		},
		chooseVideo: function() {
			uni.chooseVideo({
				maxDuration:5,
				success: (res) => {
					console.log(JSON.stringify(res));
					console.log(res.size);
					
					this.src = res.tempFilePath;
					this.uploadImage(this.src);
					console.log(this.src);
				}
			})
		},

	},
	onShow: function() {
		this.islogin();
		this.getcomlist();
	}
}



